import api from '../../../api/index.js'
export default {
  data() {
    return {
      // 遮罩层
      maskBox: false,
      activeName: 'first',

      // 保存中英日韩商品信息
      cnContent: {},
      enContent: {},
      krContent: {},
      jaContent: {},
      // 品牌名
      brandName: {},
      brandNameValue: '',
      // 品牌类目
      brandCategory: {},
      brandCat: '',
      brandCategoryValue: '',
      //类目code
      categoryCode: "",
      //存data
      categoryLevel: '',
      // code数组
      codeArr: [],
      // 一、二、三、四级类目
      catLv1: [],
      catLv2: [],
      catLv3: [],
      catLv4: [],
      // 商品ID
      brandId: '',
      // 商品单位
      goodsUnit: [{
        value: '选项1',
        label: '黄金糕'
      }, {
        value: '选项2',
        label: '双皮奶'
      }, {
        value: '选项3',
        label: '蚵仔煎'
      }],
      goodsUnitValue: '',

      // 多国主图
      cnPic: '',
      enPic: '',
      krPic: '',
      jaPic: '',
      //币种
      currency: [{
        value: '选项1',
        label: '中国'
      }, {
        value: '选项2',
        label: '日本'
      }, {
        value: '选项3',
        label: '韩国'
      }],
      currencyValue: '',
      //产地
      originPlace: [{
        value: '选项1',
        label: '中国'
      }, {
        value: '选项2',
        label: '日本'
      }, {
        value: '选项3',
        label: '韩国'
      }],
      originPlaceValue: '中国',
      // 商品保质期限
      goodsShelfLife: '365',
      // 类目
      categoryLevel01: '1',
      categoryLevel02: '2',
      categoryLevel03: '3',
      categoryLevel04: '4',
      // 各语言标题
      chineseTitle: '',
      englishTitle: '',
      koreaTitle: '',
      japanTitle: '',

      // 各语言副标题
      chineseSubtitle: '',
      englishSubtitle: '',
      koreaSubtitle: '',
      japanSubtitle: '',
      // 语言多选框
      checkList: [],
      cnLanguage: true,
      enLanguage: true,
      krLanguage: true,
      jaLanguage: true,
      //option-name
      KoreanOptionName: '',
      ChineseOptionName: '',
      EnglishOptionName: '',
      JapaneseOptionName: '',
      //option-value
      KoreanOptionValue: '',
      ChineseOptionValue: '',
      EnglishOptionValue: '',
      JapaneseOptionValue: '',
      // 搜索option列表
      optionInfoItem: [{
        info: "100/100/100/Ja"
      }, {
        info: "100/100/100/Ja"
      }, {
        info: "100/100/100/Ja"
      }, {
        info: "100/100/100/Ja"
      }, {
        info: "100/100/100/Ja"
      }, {
        info: "100/100/100/Ja"
      }],
      //sku属性选择
      addSkuOptionLine: [{
        KoreanOptionName: '1',
        ChineseOptionName: '2',
        EnglishOptionName: '3',
        JapaneseOptionName: '4',
        KoreanOptionValue: '5',
        ChineseOptionValue: '6',
        EnglishOptionValue: '7',
        JapaneseOptionValue: '8'
      }],
      skuWidth: '',
      skuHeight: '',
      skuPrice: '',
      skuWeight: '',
      skuLength: '',
      skuAddedOption: [
        { optionValue01: '黑色', optionValue02: '大', UPCvalue: '', CRcodevalue: '', Hscodevalue: '', pricevalue: '', lengthvalue: '', widthvalue: '', heightvalue: '', weightvalue: '' },
        { optionValue01: '白色', optionValue02: '中', UPCvalue: '', CRcodevalue: '', Hscodevalue: '', pricevalue: '', lengthvalue: '', widthvalue: '', heightvalue: '', weightvalue: '' },
        { optionValue01: '蓝色', optionValue02: '小', UPCvalue: '', CRcodevalue: '', Hscodevalue: '', pricevalue: '', lengthvalue: '', widthvalue: '', heightvalue: '', weightvalue: '' }
      ],
      searchBarValue: '',
      optionNameValue: '颜色',
      // add sku
      optionObj: [
        { name: 'size', value: ["big", "middle", "small"] },
        { name: 'color', value: ["red", "blue", "green"] }
      ],

      optionName: [{
        value: '颜色/color/yanse/yan',
        label: '颜色/color/yanse/yan'
      }, {
        value: '尺寸/size/chicun/chi',
        label: '尺寸/size/chicun/chi'
      }],
      showSearchBox: false,
    }
  },
  created() {
    // 进入页面获取品牌名
    this.getBrand();
    // 获取币种、产地
    this.getBasicOptions();
  },
  methods: {
    // 切换tab栏
    handleClick(tab, event) {
      console.log(tab, event);
    },
    // 获取品牌名
    getBrand() {
      const that = this;
      $.ajax({
          url: api.getBrand(),
          type: 'GET',
          dataType: 'json',
        })
        .success(function(data) {
          that.brandName = data.data.brandList;
          that.goodsUnit = data.data.unit;
          console.log(data);
        })
        .error(function() {
          that.$message({ showClose: true, message: "error", type: 'error' })
        })
        .complete(function() {
          console.log("complete");
        });
    },
    // 根据品牌名获取信息
    getBrandInfo() {
      const that = this;
      $.ajax({
          url: api.getBrandInfo(that.brandNameValue),
          type: 'GET',
          dataType: 'json',
        })
        .success(function(data) {
          if (data.data.list) {
            that.brandCategory = data.data.list;
            that.categoryLevel = data.data.cateStru;
            that.brandCategoryValue = that.brandCategory[Object.keys(that.brandCategory)[0]].allVal;
            that.brandCat = that.brandCategoryValue;
            console.log(data)
            let brandid = ($('.brandlist.selected').data('id')) ? $('.brandlist.selected').data('id') : "";
            console.log(brandid)
            that.brandId = brandid
          }
        })
        .error(function() {
          that.$message({ showClose: true, message: "error", type: 'error' })
        })
        .complete(function() {
          console.log("complete");
        });
    },
    // 选择品牌类目
    chooseBrandCat() {
      const that = this;
      setTimeout(function() {
        that.categoryCode = $(".el-select-dropdown__item.brandCats.selected").data('id')
        that.brandCat = that.brandCategoryValue;
        let arr = that.getCatCode(that.categoryCode);
        that.catLv1 = '';
        that.catLv2 = '';
        that.catLv3 = '';
        // 判断有几级，分别渲染
        if (arr[0]) {
          that.catLv1 = that.categoryLevel;
          setTimeout(function() { $('#select_category_lv1').val(arr[0]) }, 50)
          if (arr[1]) {
            that.catLv2 = that.catLv1[arr[0]].sec;
            setTimeout(function() { $('#select_category_lv2').val(arr[1]) }, 50)
            if (arr[2]) {
              that.catLv3 = that.catLv2[arr[1]].thr;
              setTimeout(function() { $('#select_category_lv3').val(arr[2]) }, 50)
            }
          }
        }
      }, 50)
    },
    //lv1点击
    catLv1Click() {
      this.catLv3 = ''
      let id = event.currentTarget.getAttribute("value");
      let name = event.currentTarget.innerText;
      this.catLv2 = this.categoryLevel[id].sec;
      this.categoryCode = id;
      this.brandCat = name;
    },
    //lv2点击
    catLv2Click() {
      let id = event.currentTarget.getAttribute("value");
      let name = event.currentTarget.innerText;
      this.catLv3 = this.catLv2[id].thr;
      this.categoryCode = id;
      this.brandCat = name;
    },
    //lv3点击
    catLv3Click() {
      let id = event.currentTarget.getAttribute("value");
      let name = event.currentTarget.innerText;
      this.categoryCode = id;
      this.brandCat = name;
    },

    //获取 多级品牌类目Id
    getCatCode(id) {
      let [key1, key2, key3, level1, level2, level3, mod1, mod2, num] = ["", "", "", "", "", "", "", "", 0];
      num = Number(id.substring(1));
      level1 = parseInt(num / 10000000);
      mod1 = num % 10000000;
      key1 = "C0" + level1 * 10000000;
      if (level1 && mod1 != 0) {
        level2 = parseInt(mod1 / 10000);
        mod2 = mod1 % 10000;
        key2 = "C0" + (level1 * 10000000 + level2 * 10000);
        if (level2 && mod2 != 0) {
          level3 = mod2 / 10;
          key3 = "C0" + (level1 * 10000000 + level2 * 10000 + level3 * 10);
        }
      }
      return [key1, key2, key3];
    },

    // 获取币种、产地
    getBasicOptions() {
      const that = this;
      $.ajax({
          url: api.getBasicOptions(),
          type: 'GET',
          dataType: 'json',
        })
        .success(function(data) {
          // that.currency = data.data.brandList;
          // that.goodsUnit = data.data.unit;
          console.log(data);
        })
        .error(function() {
          that.$message({ showClose: true, message: "error", type: 'error' })
        })
        .complete(function() {
          console.log("complete");
        });
    },
    /**
     * 语言选择，input toggle
     */
    toggleCheck(event) {
      (event.indexOf("中文") != -1) ? this.cnLanguage = false: this.cnLanguage = true;
      (event.indexOf("英文") != -1) ? this.enLanguage = false: this.enLanguage = true;
      (event.indexOf("韩文") != -1) ? this.krLanguage = false: this.krLanguage = true;
      (event.indexOf("日文") != -1) ? this.jaLanguage = false: this.jaLanguage = true;
    },

    // 上传图片
    updatePic() {
      let that = this;
      let lang = event.currentTarget.className
      console.log(that[lang])
      //创建FormData对象
      let data = new FormData();
      //为FormData对象添加数据
      $('.updatePicForm').each(function(index, el) {
        $.each($(this).find('input')[0].files, function(index, val) {
          data.append('file', val)
        });
      });
      $.ajax({
          url: api.updatePic(),
          type: 'POST',
          dataType: 'JSON',
          contentType: false,
          processData: false,
          data: data,
          cache: false,
        })
        .success(function(data) {
          console.log(data)
          that[lang] = data.data.cdnAddr;
        })
        .error(function() {
          console.log("error");
        })
        .complete(function() {
          console.log("complete");
        });

    },



    openSearchBox() {
      this.showSearchBox = true;
    },
    closeSearchBox() {
      this.showSearchBox = false;
    },
    searchOptionName() {

    },
    // 删除sku属性行
    delSkuOption(index) {
      this.addSkuOptionLine.splice(index, 1);
    },
    handleClose(done) {
      done();
    },
    // 添加option信息
    addOptionToLsits() {
      let defaultOption = ["Kr", "Cn", "En", "Ja"];
      let addOption = [];
      let addStr = '';
      let html = '';
      $('.option-add').find('input').each(function(index, el) {
        $(this).data('default', defaultOption[index]);
        ($(this).val()) ? addOption.push($(this).val()): addOption.push($(this).data('default'));
      });
      addStr = addOption.join('/');
      this.optionInfoItem.push({ info: addStr })
    },
    // 选择option信息
    chooseOptionItem(event) {
      $('.active-option.option-info-item').removeClass('active-option')
      event.currentTarget.setAttribute('class', 'active-option option-info-item');
    },
    // 保存option信息
    saveOptionInfo() {
      console.log(1)
      if (this.optionNameValue && $('.option-info').find('.active-option.option-info-item')) {
        let optionArr = this.optionNameValue.split('/');
        let valueArr = $('.option-info').find('.active-option.option-info-item').text().split("/");
        this.addSkuOptionLine.push({
          KoreanOptionName: optionArr[0],
          ChineseOptionName: optionArr[1],
          EnglishOptionName: optionArr[2],
          JapaneseOptionName: optionArr[3],
          KoreanOptionValue: valueArr[0],
          ChineseOptionValue: valueArr[1],
          EnglishOptionValue: valueArr[2],
          JapaneseOptionValue: valueArr[3]
        });
        this.showSearchBox = false;
      }
    },
    // 应用sku，生成sku信息
    applyToBuild() {
      let that = this;
      this.optionObj = [];
      $('.added-sku-option').each(function(index, el) {
        let obj = {};
        let item = $(this).find('.option_name').find('.el-col').first().next().find('input');
        let value = $(this).find('.option_value').find('.el-col').first().next().find('input');

        console.log(this.optionObj)
      });
      console.log(that.optionNameArr)
      console.log(that.optionValueArr)
    }
  },
  watch: {
    // 批量输入
    skuPrice: function() {
      let that = this;
      $('.price-value').each(function() {
        $(this).find('input').val(that.skuPrice)
      })
    },
    skuLength: function() {
      let that = this;
      $('.length-value').each(function() {
        $(this).find('input').val(that.skuLength)
      })
    },
    skuHeight: function() {
      let that = this;
      $('.height-value').each(function() {
        $(this).find('input').val(that.skuHeight)
      })
    },
    skuWeight: function() {
      let that = this;
      $('.weight-value').each(function() {
        $(this).find('input').val(that.skuWeight)
      })
    },
    skuWidth: function() {
      let that = this;
      $('.width-value').each(function() {
        $(this).find('input').val(that.skuWidth)
      })
    },
  },
}




